.intro{
    background-color: white;
    display:flex;

    .left{
        flex: 0.5;
        overflow: hidden;

        .imgContainer{
            width: 700px;
            height: 700px;
            background-color: rgb(223, 237, 243);
            border-radius: 10%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: right;
            img{
                height: 50%;
                //border-radius: 50%;
                width: 70%;
            }
        }
    }


    .right{
        flex: 0.5;
        position: relative;
        //overflow: hidden;
        .wrapper{
            width: 100%;
            height: 60%;
            padding-left: 50px;
            padding-bottom:10px;
            display:flex;
            flex-direction: column;
            justify-content: center;

            h1{
                font-size: 50px;
                margin: 10px 0;
            }

            h2{
                font-size: 35px;
            }

            h3{
                font-size: 30px;
            }
            a{
                position: absolute;
                left: 40%;

                img{
                    width: 10px;
                    -webkit-animation: 3s soft-blink infinite alternate;

                    //animation: arrowBlink 2s infinite;
                    animation: 3s soft-blink infinite alternate;
                    
                }
                @keyframes arrowBlink{
                    100%{
                        opacity: 0;
                    }
                    0%{
                        opacity: 100;
                    }
                }

            }
        }
    }
}