    display: flex;

    .left{
        display: flex;
        flex: 1.2;
        align-items: center;
        justify-content: center;
        padding-left: 50px;
        padding-right: 0px;
        background-color: lightgray;

        .container{
            width: 500px;
            //height: 200px;
            background-color: lightgray;
            border-radius: 10%;
            display: flex;
            align-items: center;
            justify-content: center;
            float: left;

        .title {
            font-size: 35px;
            color:black;
