.works{
    background-color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    .arrow{
        height: 70px;
        position: absolute;
        cursor: pointer;

        &.left{
            left: 100px;
            transform: rotateY(180deg);
        }

        &.right{
            right: 100px;
        }
    }

    .slider{
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        transition: all 1s ease-out;

        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item{
                width: 800px;
                height: 100%;
                background-color: crimson;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;
            }
            .left{
                flex: 4;
                height: 100%;
                display:flex;
                align-items: center;
                justify-content: center;
                
                .leftContainer{
                    width: 90%;
                    height: 90%;
                    display: flex;
                    flex-direction: column;
                    justify-content: space-around;

                    .imgContainer{
                        width: 80px;
                        height: 80px;
                        border-radius: 50%;
                        background-color: white;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        

                        img{
                            width: 90px;
                        }
                    }
                    h2{
                        font-size: 25px;
                        color: white;
                       
                    }
                    p{

                        font-size: 15px;
                        color: white;

                    }
                    a{

                        font-size: 14px;
                        color: black;

                        font-weight: 600;
                        text-decoration: underline;
                        cursor: pointer;
                    }


                }
            }

            .right{
                flex:8;
                height: 100%;
                display: flex;
                .img{
                    width: 400px;
                }
            }



        
        }

    }
    

    
}